*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    width: 100%;
    margin: 0 auto;
}

.container-login{
    width: 100%;
    min-height: 100vh;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    padding: 15px;

    background-color: rgb(243, 248, 246);
}

.wrap-login{
    width: 390px;
    background-color: rgb(236, 240, 235);
    border: 0.5em solid;
    border-radius: 40px;

    overflow: hidden;
    padding: 77px 55px 33px 55px;

    box-shadow: 0.5px 10px 0px rgb(0, 0, 0, 0.1);
}

.login-form{
    width: 100%;
}

.login-form-title{
    display: block;
    font-size: 30px;
    color: rgb(0, 0, 0);
    font-weight: bold;
    line-height: 1.2;
    text-align: center;
}

.login-form-title img {
    width: 90px;
}

.wrap-input{

    width: 100%;
    position: relative;
    border-bottom: 3px solid #198754;
    margin-bottom: 37px; 
}

.input{
    font-size: 120%;
    color: #070707;
    line-height: 1.2;
    border: none;
    display: block;
    

    width: 100%;
    height: 45px;

    background-color: transparent;

    padding: 0.5px;
}


.focus-input{
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    font-weight: bold;
    top: 0;
    left: 0;

    pointer-events: none;
    color: #141313;
}

.focus-input::before {
    content: "";
    display: block;
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 2px;

    

    background-color: #198754;

} 

.focus-input::after{
    font-size: 15px;
    color: #131313;
    line-height: 1.2;

    content: attr(data-placeholder);

    display: block;
    width: 100%;
    position: absolute;
    top: 16px;
    left: 0px;

    padding-left: 5px;

   
}

.input:focus{
    outline: 0;
}

input:focus + .focus-input::after {
    top: -15px;
}

.input:focus + .focus-input::before {
    width: 100%;
}

.has-val + .focus-input::after {
    top: -15px;
}

.has-val + .focus-input::before {
    width: 100%;
}



.container-cadastra-form-btn {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 13px;
}

.cadastra-form-btn {
    font-size: 15px;
    border: none;
    border-radius: 10px;
    color: #efe5e5;

    line-height: 1.2;

    text-transform: uppercase;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 50px;

    background-color: #198754;
}

.cadastra-form-btn:hover{
    cursor: pointer;
}